import React from "react";
import Picture from "./picture"

const User = (props) => {
    
    let header = <header className="header_mobile">

        <p className="site-logo mobile">Andrey Markushevsky</p>
    </header>
    let footer = 
    <footer>
      <p>Create by myself with love...</p>
    </footer>
    return (
        <>
        {header}
        <Picture className="mobile"/>
        {footer}
        </>
    );
}

export default User;